}
        
        
.team-list {
display: flex;
justify-content: center;
flex-wrap: wrap;
column-gap: 24px;
row-gap: 64px;
margin: 0 auto;
}
        
        
.icons-symbol {
display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
padding: 4px 0px;
gap: 24px;
width: 232px;
height: 48px;
margin-left: auto;
margin-right: auto;
margin-top: 8px;
cursor: pointer;
         
}
        
        
.border-mobile {
margin-bottom: 0px;
}
        
}
        
@media screen and (min-width: 768px) and (max-width: 1199px){
.skils-mobile2 {
