.button-read {
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    width: 164px;
    height: 42px;

    background: var(--main-yellow);
    border-radius: 10px;
    border-color: var(--main-yellow);
}

.advantages-mass {
    width: 100%;
}

.cov-fone {
    display: flex;
    background-color: var(--main-yellow);
    border-radius: 25px;

    width: 50px;
    height: 50px;
    margin-left: auto;
    margin-right: auto;

}

.cow-icon {
    justify-content: center;
    margin-top: 7px;
    margin-left: 7px;
    fill: #131313;


}

.advantages-nomber-mass {
    font-weight: 700;
    font-size: 36px;
    line-height: 1.22;

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    margin-left: auto;
    margin-right: auto;

    color: var(--text-light);

    flex: none;
    order: 1;
    flex-grow: 0;
}

.advantages-text-mass {
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;

    text-align: center;

    color: var(--text-light);

    flex: none;
    order: 2;
    flex-grow: 0;
}